const MTOKEN_ADDR = "0x5094088689BFc5EF71B34dE0Faa0D523baA7DD55";
const FARM_ADDR = "0x915A043bd58e6045B971a4E602c4fae43365987F";
const USER_REF = "0xb7ca6e69D7d14aE9F068037F4B10Fb62F1DA8d19"


//正式版 
// const MTOKEN_ADDR = "0x4122537f9354c995dbaf8c03f6f9d11fa515f8af";
// const FARM_ADDR = "0x9a4004e5a58079190f0d693ef253aee5ec0eda41";
