class IDO{
    constructor(_web3, _account) {
        this.web3           = _web3;
        this.account        = _account;
        this.contract_mtoken   = new this.web3.eth.Contract(MTOKEN_ABI, MTOKEN_ADDR);
        this.contract_farm  = new this.web3.eth.Contract(FARM_ABI, FARM_ADDR);
        console.log(this.contract_farm)
    }

    async index_page() {
        let that = this;
        // $("#addr").text(this.account);
        const result = await this.contract_farm.methods.query_account(this.account).call();
    

        this.activation     = result[0]; //是否激活
        this.ref_addr       = result[1]; // 他的推荐者地址
        this.farm_allowance = Number(this.web3.utils.fromWei(result[2])); // 合约授权的金额数量
        this.balance        = Number(this.web3.utils.fromWei(result[3])); // 当前地址的BNB或TRX余额
        this.mtoken_balance    = Number(this.web3.utils.fromWei(result[4]));   // 当前地址的MTOKEN余额
        this.bridge_balance = Number(this.web3.utils.fromWei(result[3]));  // 当前地址的BRIDGE余额



        const stake_result = await this.contract_farm.methods.query_stake(this.account).call();

        console.log('query_stake数据:',stake_result)

     
        this.stake_amount = Number(this.web3.utils.fromWei(stake_result[0])); // 当前地址质押的MTOKEN金额
        this.shares = stake_result[1] //用户算力总数
        this.rewardDebt = Number(this.web3.utils.fromWei(stake_result[2])); // rewardDebt：用户奖励负债

        this.reward_amount = Number(this.web3.utils.fromWei(stake_result[3])); // 挖到的BRIDGE金额

        let obj = {}
          obj.balance = this.balance
          obj.bridge_balance = this.bridge_balance
          obj.mtoken_balance = this.mtoken_balance
          obj.farm_allowance = this.farm_allowance
          obj.ref_addr = this.ref_addr
          obj.rewardDebt = this.rewardDebt
          obj.reward_amount = this.reward_amount
          obj.stake_amount = this.stake_amount
          obj.shares = this.shares
          return obj
    }

    getQueryVariable(variable) {
        let href = window.location.href
        let query = href.substring(href.indexOf('?')+1);
        let vars = query.split("&");
        for (var i = 0; i < vars.length; i++) {
            let pair = vars[i].split("=");
            if (pair[0] == variable) {
                return pair[1];
            }
        }
        return (false);
    }

    async deposit(amount) {
      if(amount >= 0){
        let that = this;
        let amount1 = amount
        let ref = ''
        if(this.activation){
          ref = this.ref_addr
        } else{
          if(this.getQueryVariable('ref')){
            ref = this.getQueryVariable('ref')
          } else{
            ref = USER_REF
          }
        }
        console.log(amount1, ref,this.account)
        await this.contract_farm.methods.deposit(this.web3.utils.toWei(amount1), ref).send({from: this.account}).then((tx) => {
            console.log(tx)
            alert("Waiting for confirmation,the hash is：" + tx.transactionHash);
            that.waitForReceipt(tx.transactionHash, 6, (receipt) => {
                console.log("result: " + receipt.status);
                return true
                // that.index_page(); // reload
            });
        });
      } else{
        alert("The number should be more than 0");
      }
    }

    async withdraw() {
      let that = this;
        let amount1 = this.stake_amount.toString()
        let ref = ''
        if(this.activation){
          ref = this.ref_addr
        } else{
          if(this.getQueryVariable('ref')){
            ref = this.getQueryVariable('ref')
          } else{
            ref = USER_REF
          }
        }
        await this.contract_farm.methods.withdraw().send({from: this.account}).then((tx) => {
            console.log(tx)
            alert("Waiting for confirmation,the hash is：" + tx.transactionHash);
            that.waitForReceipt(tx.transactionHash, 6, (receipt) => {
                console.log("result: " + receipt.status);
                return true
                // that.index_page(); // reload
            });
        });
    }

    async claim() {
      let that = this;
      await this.contract_farm.methods.claim().send({from: this.account}).then((tx) => {
        alert("Waiting for confirmation,the hash is：" + tx.transactionHash);
        that.waitForReceipt(tx.transactionHash, 6, (receipt) => {
          console.log("result: " + receipt.status);
          return true
        });
      });
    }

    async waitForReceipt(tx_hash, max_try, cb) {
      console.log(tx_hash, max_try)
        if(max_try <= 0){
            alert("waitForReceipt timeout");
            return;
        }

        let receipt = await this.web3.eth.getTransactionReceipt(tx_hash);
        if (receipt != null) {
            cb(receipt);
            // receipt struct:
            // {
            // "status": true,
            // "transactionHash": "0x9fc76417374aa880d4449a1f7f31ec597f00b1f6f3dd2d66f4c9c6c445836d8b",
            // "transactionIndex": 0,
            // "blockHash": "0xef95f2f1ed3ca60b048b4bf67cde2195961e0bba6f70bcbea9a2c4e133e34b46",
            // "blockNumber": 3,
            // "contractAddress": "0x11f4d0A3c12e86B4b5F39B213F7E19D048276DAe",
            // "cumulativeGasUsed": 314159,
            // "gasUsed": 30234,
            // "logs": [{
            //     // getPastLogs 等返回的日志对象
            // }, ...]}
        } else {
          console.log(tx_hash, max_try)
            await this.sleep(1500);
            this.waitForReceipt(tx_hash, max_try - 1, cb);
        }
    }

    sleep(ms) {
        return new Promise((resolve) => {
            setTimeout(resolve, ms);
        });
    }

