    async approve(){
        //不能输入0
        if(this.num <=0 ){
            this.$toast("The number should be more than 0")
            return false
        }
        //金额不能大于余额
        if(this.num > this.ido.mtoken_balance){
           this.$toast("The amount should not exceed the EMT balance")
           return false
        }
        return new Promise((resolve, reject) => {
              this.$toast.loading({
                duration: 0, // 持续展示 toast
                message: 'loading...',
                forbidClick: true,
              });
             if(this.ido.balance == 0 || this.ido.mtoken_balance == 0){
                alert("Failed to connect to wallet, please confirm current account balance");
                return false
            } 
            let allowanceType = true;
            if(this.ido.farm_allowance < this.ido.mtoken_balance){
                  allowanceType = false;
                  alert("This operation authorises"+this.num+"EMT to smart contract");
                  this.$toast.loading({
                      duration: 0, // 持续展示 toast
                      message: "Please Wait...",
                      forbidClick: true,
                  });
                  this.idoBolle.contract_mtoken.methods
                  .approve(FARM_ADDR, this.idoBolle.web3.utils.toWei('9999999999'))
                  .send({from: this.idoBolle.account})
                  .then((tx) => {
                    alert("Waiting for confirmation,the hash is：" + tx.transactionHash);
                    this.idoBolle.waitForReceipt(tx.transactionHash, 6, (receipt) => {
                        this.$toast.clear();
                            this.$dialog.confirm({
                            message: 'Confirming deposit?',
                            confirmButtonText: 'Yes',
                            cancelButtonText: 'No',
                            theme: 'round-button',
                          }).then(() => {
                              this.deposit()
                          }).catch(() => {
                          });
                    });
                  }).catch(() => {
                    this.$toast.clear();
                  });
            }
            console.log('审核状态:'+allowanceType)
            if(allowanceType){
                  this.$toast.clear();
                  this.$dialog.confirm({
                  message: 'Confirming deposit?',
                  confirmButtonText: 'Yes',
                  cancelButtonText: 'No',
                  theme: 'round-button',
                }).then(() => {
                    this.deposit()
                }).catch(() => {
                    this.$toast.clear();
                });
            }                                                   
      }).then(key =>{
              
      })

},
    withdraw(){
